import React from 'react';
import OurSectionItem from "./OurSectionItem/OurSectionItem";
import './OurSection.css';
import ScrollAnimation from 'react-animate-on-scroll';

const OurSection = props => (
    <div className="OurSection">
        <ScrollAnimation
            delay={100}
            animateIn='fadeIn'
            duration={3}
            initiallyVisible={false}>
            <OurSectionItem
                bigNumber='01' title=' '
                text=' Neobis    2016   , - ,   .'
            />
        </ScrollAnimation>
        <ScrollAnimation
            delay={400}
            animateIn='fadeIn'
            duration={3}
            initiallyVisible={false}>
            <OurSectionItem
                bigNumber='02' title=' '
                text='  IT         '
            />
        </ScrollAnimation>
        <ScrollAnimation
            delay={600}
            animateIn='fadeIn'
            duration={3}
            initiallyVisible={false}>
            <OurSectionItem
                bigNumber='03' title=' '
                text='            IT   '
            />
        </ScrollAnimation>
    </div>
);

export default OurSection;


